import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import MovieList from './components/MovieList'
import SearchBox from './components/SearchBox';

const App = () => {
	const [searchValue, setSearchValue] = useState('');
	const [movies, setMovies] = useState([
		{
			Title: 'Natsamrat',
			Year: '1977',
			imdbID: 'tt0076759',
			Type: 'movie',
			Poster:
				'https://m.media-amazon.com/images/M/MV5BYjkzZWIyZTctN2U3Ny00MDZlLTkzZTYtMTI2MWI5YTFiZWZkXkEyXkFqcGdeQXVyNTM2NTg3Nzg@._V1_SX300.jpg',
		},
		{
			Title: 'ABCD(Any Body Can Dance)',
			Year: '2013',
			imdbID: 'tt0080684',
			Type: 'movie',
			Poster:
				'https://m.media-amazon.com/images/M/MV5BMTU3NTk0NjE5Nl5BMl5BanBnXkFtZTcwMTU2ODMwOQ@@._V1_SX300.jpg',
		},
		{
			Title: 'Our Cartoon President',
			Year: '2018',
			imdbID: 'tt0086190',
			Type: 'movie',
			Poster:
				'https://m.media-amazon.com/images/M/MV5BMTczZWU5ZmYtMWVjMi00Yzg1LWE2ZjgtYmEzYWE3MDlmMTAzXkEyXkFqcGdeQXVyOTA3MTMyOTk@._V1_SX300.jpg',
		},
		{
			Title: 'Kala',
			Year: '2021',
			imdbID: 'tt0086190',
			Type: 'movie',
			Poster:
				'https://m.media-amazon.com/images/M/MV5BYzA2MGJiNzgtZTkyYy00OTg1LThiYjUtYjQwZDJmN2Q1OWJmXkEyXkFqcGdeQXVyMjkxNzQ1NDI@._V1_SX300.jpg',
		}
	]);
	 


	const getMovieRequest = async (searchValue) => {
		const url = `http://www.omdbapi.com/?s=${searchValue}&apikey=263d22d8`;

		const response = await fetch(url);
		const responseJson = await response.json();
		console.log(responseJson);

		if (responseJson.Search) {
			setMovies(responseJson.Search.slice(0,4));
		}
	};

	useEffect(() => {
		getMovieRequest(searchValue);
	}, [searchValue]);

	return (
		<div className='container-fluid movie-app'>
			
			<div className='row d-flex align-items-center mt-4 mb-4'>
				
				<SearchBox searchValue={searchValue} setSearchValue={setSearchValue} />
			</div>
			<div className='row'>
				<MovieList movies={movies} />
			</div>
		</div>
	);
};

export default App;